var host = "http://127.0.0.1:5000/";

export default {
	/**
	 * 
	 * @param {Object} Vue vue
	 * @param {Object} optons 
	 */
	install(Vue, optons) {
		if (optons && optons.host) {
			host = optons.host;
		}

		var redirect_url = "";
		Vue.mixin({
			data() {
				return {}
			},
			methods: {
				$message(content, type = "none") {
					uni.showToast({
						title: content,
						icon: type,
						duration: 2000
					});
				},
				/**
				 * 
				 * @param {String} url 
				 * @param {String} name 
				 */
				$download(url, name) {
					var anchor = document.createElement('a');
					if (!name) {
						var arr = url.split("/");
						name = arr[arr.length - 1];
					}
					if ('download' in anchor) {
						anchor.href = url.replace("~/", host);
						anchor.setAttribute("download", name);
						anchor.className = "download-js-link";
						anchor.style.display = "none";
						document.body.appendChild(anchor);
						setTimeout(function() {
							anchor.click();
							document.body.removeChild(anchor);
						}, 66);
						return true;
					}
				},
				/**
				 * 
				 * @param {String} url 
				 */
				$nav(url) {
					if (url.indexOf("//") === 0) {
						uni.switchTab({
							url: url.replace('/', "")
						});
					} else if (url) {
						uni.navigateTo({
							url
						})
					} else {
						uni.navigateBack({
							delta: 2
						});
					}
				},
				/**
				 * @description 
				 * @param {Function} func 
				 */
				$get_user(func) {
					var _this = this;

					this.$get('~/api/user/state', null, function(json, status) {
						if (json.result) {
							_this.$store.commit('user_set', json.result);
							if (func) {
								func();
							}
						} else if (json.error) {
							// 
							_this.$store.commit('sign_out');
						} else {
							_this.$toast('');
						}
					});
				},
				/**
				 * 
				 * @param {String} url
				 */
				$redirect(url) {
					if (url) {
						redirect_url = url;
					} else {
						return redirect_url;
					}
				},
				/**
				 * @description 
				 * @param {String} text 
				 * @param {String} type 
				 */
				$toast(text, type = 'dark') {
					uni.showToast({
						title: text,
						icon: "none",
						duration: 2000
					});
				},
				/**
				 * 
				 * @param {String} time 
				 * @param {String} format 
				 */
				$toTime(time, format) {
					return time.toTime().toStr(format);
				},
				/**
				 * @description 
				 * @param {Array} arr 
				 * @param {String} key 
				 * @param {Object} value 
				 * @return {Array} 
				 */
				$filter(arr, key, value) {
					var ar = [];
					for (var i = 0; i < arr.length; i++) {
						var o = arr[i];
						if (o[key] === value) {
							ar.push(o);
						}
					}
					return ar;
				},

				/**
				 * @description url
				 * @param {Object} obj 
				 * @param {String} url 
				 * @return {String} url
				 */
				$toUrl(obj, url) {
					var queryStr = "";
					for (var key in obj) {
						var value = obj[key];
						if (typeof(value) === 'number') {
							if (value > 0) {
								queryStr += "&" + key + "=" + obj[key];
							}
						} else if (value) {
							queryStr += "&" + key + "=" + encodeURI(value);
						}
					}
					if (url) {
						if (url.endWith('?') || url.endWith('&')) {
							return url + queryStr.replace('&', '');
						} else if (url.indexOf('?') === -1) {
							return url + queryStr.replace('&', '?');
						} else {
							return url + queryStr;
						}
					} else {
						return queryStr.replace('&', '');
					}
				},
				/**
				 * 
				 * @param {Array} list 
				 */
				$toName(list, value, value_key = 'name', key = 'value') {
					var ret = "";
					for (var i = 0; i < list.length; i++) {
						var o = list[i];
						if (o[key] === value) {
							ret = o[value_key];
						}
					}
					return ret;
				},
				/**
				 * url
				 * @param {String} url 
				 * @return {String} 
				 */
				$fullUrl(url) {
					var url_new = "";
					if (url) {
						if (url.indexOf("~/") === 0) {
							url_new = url.replace('~/', host);
						} else if (url.indexOf("/") === 0) {
							url_new = url.replace('/', host);
						} else {
							url_new = url;
						}
					}
					return url_new;
				},
				/**
				 * GET
				 * @param {String} url 
				 * @param {Object} body 
				 * @param {Funciton} func await
				 * @return {Object} 
				 */
				async $get(url, body, func) {
					url = url.replace('~/', host);
					if (body) {
						url = this.$toUrl(body, url);
					}
					if (func) {
						uni.request({
							url, // 
							method: "GET",
							header: {
								'x-auth-token': this.$store.state.user.token
							},
							dataType: "json",
							success: function(res) {
								func(res.data);
							},
							error: function(err) {
								func(err);
							}
						});
					} else {
						var [error, res] = await uni.request({
							url, // 
							header: {
								'x-auth-token': this.$store.state.user.token
							},
							dataType: "json",
							data
						});
						return res.data;
					}
				},
				/**
				 * POST
				 * @param {String} url 
				 * @param {Object} data 
				 * @param {Funciton} func await
				 * @return {Object} 
				 */
				async $post(url, data, func) {
					url = url.replace('~/', host);
					if (func) {
						uni.request({
							url, // 
							method: "POST",
							header: {
								'x-auth-token': this.$store.state.user.token,
								'Content-Type': 'application/json'
							},
							dataType: "json",
							data,
							success: function(res) {
								func(res.data);
							},
							error: function(err) {
								func(err);
							}
						});
					} else {
						var [error, res] = await uni.request({
							url, // 
							method: "POST",
							header: {
								'x-auth-token': this.$store.state.user.token,
								'Content-Type': 'application/json'
							},
							dataType: "json",
							data
						});
						return res.data;
					}
				},
				/**
				 * 
				 * @param {String} url 
				 * @param {Object} body 
				 * @param {Funciton} func await
				 * @return {Object} 
				 */
				async $upload(url, body, func) {
					url = url.replace('~/', host);
					//  - 
					uni.setHeader('x-auth-token', this.$store.state.user.token);
					//  - 
					uni.setHeader('Content-Type', 'multipart/form-data', ['post']);
					if (func) {
						// , 
						uni.post(url, body).then((res) => {
							func(res.data);
						}).catch((res) => {
							func(res);
						});
					} else {
						// 
						var res = await uni.post(url, body);
						return res.data;
					}
				},
				/**
				 *  
				 * @param {String} action 
				 */
				$check_action(path, action = "get") {
					var o = this.$get_power(path);
					console.log(path,o);
					if(!o){
						return true;
					}
					return o[action];
				},
				
				/**
				 * 
				 * @param {String} user_group 
				 */
				async $get_auth(user_group = "") {
					this.$get("~/api/auth/get_list?", {
						user_group
					}, (json) => {
						this.$store.commit("set_auth", json.result.list);
					});
				},
				
				/**
				 *  
				 * @param {String} path 
				 */
				$get_power(path) {
					var list = this.$store.state.web.auth;
					var obj;
					for (var i = 0; i < list.length; i++) {
						var o = list[i];
						if (o.path === path) {
							obj = o;
							break;
						}
					}
					return obj;
				},
				
				/**
				 * 
				 * @param {String} action 
				 * @param {String} field 
				 */
				$check_field(action, field) {
					var o = this.$get_power(this.$route.path);
					var auth;
					if (o && o[action]) {
						auth = o[action]["field_" + action];
					}
					if (auth) {
						return auth.indexOf(field) !== -1;
					}
					return false;
				}
			}
		})
	}
}
